/* ----------------------------------------- */
/* Configuration
/* ----------------------------------------- */

$primary-color: #222831 !default;
$secondary-color: #30475e !default;
$tertiary-color: #faa916 !default;
$dark-color: #363a44 !default;
$light-color: #dae1e7 !default;

$sans-serif: 'Inter', system, -apple-system, sans-serif !default;
$serif: 'Eczar', 'Signika', Georgia, serif !default;

$btn-color: #8e0317 !default;
$btn-radius: 0 !default;

/* ----------------------------------------- */
/* Fonts
/* ----------------------------------------- */

@import url('../fonts/Inter/inter.css');
@import url('../fonts/Eczar/eczar.css');
@font-face {
  font-family: 'Pathfinder2eActions';
  src: url('../fonts/Pathfinder2eActions.ttf');
}

/* ----------------------------------------- */
/* Global UI
/* ----------------------------------------- */

:root {
  --primary: #{$primary-color};
  --secondary: #{$secondary-color};
  --tertiary: #{$tertiary-color};
  --light: #{$light-color};
  --dark: #{$dark-color};
}

body {
  font-family: $sans-serif;
}

/* ----------------------------------------- */
/* Typography
/* ----------------------------------------- */

body {
  font-weight: 500;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 600;
}

p {
  line-height: 1.5;
}

button {
  font-family: $serif;;
}
